
.col {
  display: flex;
  justify-content: space-around;
}

.basicInput {
  display: flex;
  
  flex-direction: column;
  width: 60%;

  /* background-color:aqua; */
}

.label {
  display: flex;
  text-align: left;
 
  flex-direction: column;
}

.select {
  width: 50%;
  height: 50px;
  border-radius: 5px;
  box-shadow: none;
  border: 1px solid #ced6e0;
  transition: all 0.3s ease-in-out;
  font-size: 18px;
  padding: 5px 15px;
  background: none;
  color: #000000;
  margin: 0.5em 0;
  padding-left: 50px;
  cursor: pointer;
}

